<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right mt-4">
      <button class="btn btn-primary" @click="openCouponModal(true)">Create New Coupon</button>
    </div>
    <div class="table-responsive">
        <table class="table table-hover mt-4">
            <thead>
                <tr>
                <th width="100">Name</th>
                <th class="text-center">Discount percent ( % )</th>
                <th class="text-center">Expiry date</th>
                <th class="text-center">Enable/Disable</th>
                <th width="130">Edite</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(list, key) in coupons" :key="key">
                <td>{{ list.title }}</td>
                <td class="text-center">{{ list.percent }}%</td>
                <td class="text-center">{{ list.due_date | data }}</td>
                <td>
                    <span v-if="list.is_enabled === 1" class="text-success d-block text-center">Enable</span>
                    <span v-else class="text-danger d-block text-center">Disable</span>
                </td>
                <td>
                    <div class="d-flex">
                        <button class="btn btn-outline-primary btn-sm mx-1" @click="openCouponModal(false, list)">
                            Edite
                        </button>
                        <button class="btn btn-outline-primary btn-sm mx-1" @click=" deleteCouponModal(list)">
                            Delete
                        </button>
                    </div>
                </td>
                </tr>
            </tbody>
        </table>
    </div>
    <div class="modal fade" id="couponModal" tabindex="-1" role="dialog"aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header ms-header">
            <h5 class="modal-title ms-title text-white" id="exampleModalLabel"></h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label for="title">Title</label>
              <input type="text" class="form-control" id="title" v-model="tempCoupon.title"
                placeholder="please enter title">
            </div>
            <div class="form-group">
              <label for="coupon_code">Coupon code</label>
              <input type="text" class="form-control" id="coupon_code" v-model="tempCoupon.code"
                placeholder="please enter coupon code">
            </div>
            <div class="form-group">
              <label for="due_date">Expiry date</label>
              <input type="date" class="form-control" id="due_date"v-model="due_date">
            </div>
            <div class="form-group">
              <label for="price">Discount percent ( % )</label>
              <input type="number" class="form-control" id="price" v-model="tempCoupon.percent" placeholder="please enter discount percent">
            </div>
            <div class="form-group">
              <div class="form-check">
                <input class="form-check-input" type="checkbox" :true-value="1" :false-value="0" v-model="tempCoupon.is_enabled" id="is_enabled">
                <p v-if="tempCoupon.is_enabled" class="text-success">Enable</p>
                <p v-else class="text-danger">Disable</p>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            <button type="button" class="btn btn-text" @click="updateCoupon"></button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" id="delete" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
            <div class="modal-content border-0">
                <div class="modal-header bg-danger text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                        <span>Delete Product</span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body text-center py-4">
                    Delete the <strong class="text-danger">"{{ tempCoupon.title }}"</strong> coupon ?
                </div>
                <div class="modal-footer">
                    <p class="mx-4">If you delete the code can't be recovery !</p>
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">Cancel</button>
                    <button type="button" class="btn btn-danger" @click="deleteCoupon">Confirm</button>
                </div>
            </div>
        </div>
    </div>
    <Modal/>
  </div>
</template>

<script>
import $ from 'jquery';
import Modal from '../Modal'
export default {
    props: {
        config: Object,
    },
    data() {
        return {
            coupons: {},
            tempCoupon: {//儲存回傳優惠券資料
                title: '',
                is_enabled: 0,
                percent: 100,
                due_date: 0,
                code: '',
            },
            isLoading:false,
            due_date: new Date(),
            isNew: false,
        };
    },
    watch: {
        due_date() {
            const vm = this;
            const timestamp = Math.floor(new Date(vm.due_date) / 1000);
            vm.tempCoupon.due_date = timestamp;
        },
    },
    components:{
        Modal
    },
    methods: {
        // 開啟新增優惠券 modal
        openCouponModal(isNew, list) {
            const vm = this;
            $('#couponModal').modal('show');
            vm.isNew = isNew;
            if (vm.isNew) {
                vm.tempCoupon = {};
                $('.btn-text').text('Confirm').addClass('btn-info').removeClass('btn-danger');
                $('.ms-title').text('Create New Coupon')
                $('.modal-footer').removeClass('justify-content-center')
                $('.ms-header').addClass('bg-info').removeClass('bg-danger')
            } else {
                vm.tempCoupon = Object.assign({}, list);
                const dateAndTime = new Date(vm.tempCoupon.due_date * 1000).toISOString().split('T');
                vm.due_date = dateAndTime[0];
                $('.btn-text').text('Update').addClass('btn-danger').removeClass('btn-info');
                $('.ms-title').text('Change Coupon Content')
                $('.modal-footer').removeClass('justify-content-center')
                $('.ms-header').addClass('bg-danger').removeClass('bg-info')
            }
        },
        // 開啟刪除優惠券 modal
        deleteCouponModal(list){
            $('#delete').modal('show');
            this.tempCoupon = Object.assign({}, list);
        },
        // 取得優惠券
        getCoupons() {
            const vm = this;
            vm.isLoading = true
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons`;
            this.$http.get(url, vm.tempProduct).then((response) => {
                vm.isLoading = false
                vm.coupons = response.data.coupons;
        });
        },
        // 新增與修改優惠券
        updateCoupon() {
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
            console.log(vm.tempCoupon.id);
            if (vm.isNew) {
                    const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
                    this.$http.post(url, { data: vm.tempCoupon }).then(() => {
                    $('#couponModal').modal('hide');
                    setTimeout(()=>{
                        $('#modal-show').modal('show')
                        $('.modal-header').addClass('bg-info')
                        $('.ms-content').text('Create success !')
                        $('.seconds').text('The modal will close in 1 seconds')
                        $('.modal-footer').addClass('justify-content-center')
                        $('.btn-color').css('display','none')
                    },800)
                    setTimeout(()=>{
                        $('#modal-show').modal('hide')
                        $('.seconds').text('')
                    },1700)
                    setTimeout(()=>{
                        this.getCoupons();// 重新取得/刷新優惠券
                    },1701)
                });
            } else {
                const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
                console.log(url)
                vm.due_date = new Date(vm.tempCoupon.due_date * 1000);
                    this.$http.put(url, { data: vm.tempCoupon }).then(() => {
                    $('#couponModal').modal('hide');
                    setTimeout(()=>{
                        $('#modal-show').modal('show')
                        $('.modal-header').addClass('bg-info')
                        $('.ms-content').text('Edite success !')
                        $('.seconds').text('The modal will close in 1 seconds')
                        $('.modal-footer').addClass('justify-content-center')
                        $('.btn-color').css('display','none')
                    },800)
                    setTimeout(()=>{
                        $('#modal-show').modal('hide')
                        $('.seconds').text('')
                    },1700)
                    setTimeout(()=>{
                        this.getCoupons();// 重新取得/刷新優惠券
                    },1701)
                });
            }
        },
        // 刪除優惠券
        deleteCoupon() {
                const vm = this;
                const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
                this.$http.delete(url).then(() => {
                    $('#delete').modal('hide');
                    vm.isLoading = false
                    this.getCoupons();// 重新取得/刷新優惠券
                })
        }
    },
    created() {
        this.getCoupons();// 取得優惠券
    },
};
</script>


