<template>
<div>
<Navbar/>
    <div class="container-fluid">
        <div class="row">
            <Sidebar/>
            <main role="main" class="col-md-9 ml-sm-auto col-lg-10 px-md-4">
                <router-view/>
            </main>
        </div>
    </div>
</div>
</template>

<script>
// 全域套用頁面元件
import Navbar from './Navbar'
import Sidebar from './Sidebar'

// 設定載入頁面元件
export default {
    components:{
        Navbar,
        Sidebar
    }
}
</script>
        


