<template>
  <div>
    <form class="form-signin text-center my-5 form-style" @submit.prevent="signIn">
      <h1 class="h3 mb-3 font-weight-normal">Please sign in</h1>
      <label for="inputEmail" class="sr-only">Email address</label>
      <input v-model="user.username" type="email" id="inputEmail" class="form-control" placeholder="Account or Email address" required autofocus>
      <label for="inputPassword" class="sr-only">Password</label>
      <input v-model="user.password" type="password" id="inputPassword" class="form-control" placeholder="Password" required>
      <div class="checkbox mt-3 mb-2">
          <label>
          <input type="checkbox" value="remember-me"> Remember me
          </label>
      </div>
      <button class="btn btn-lg btn-primary btn-block" type="submit" style="overflow:hidden;">
        <span class="text d-block">Sign in</span>
      </button>
      <p class="mt-5 mb-3 text-muted">&copy; 2017-2020</p>
    </form>
    <Modal/>
  </div>
</template>

<style scoped>
.form-style{
  border-radius:5px;
  box-shadow:inset 0 0 1px 0 rgba(220,220,220,.7),0 0 1px 0px rgba(215,215,215,.7),0 3px 3px 2px rgba(210,210,210,.7),0 4px 3px 3px rgba(210,210,210,.7);
}
</style>

<script>
import $ from 'jquery'
import Modal from '../Modal'

export default {
  data () {
    return {
        user:{
            username:'',
            password:''
        }
    };
  },
  components:{
    Modal
  },
  methods: {
    // 登入成功與否跳出訊息與設定
        signIn() {
            const api = `${process.env.APIPATH}/admin/signin`;
            const vm = this
            this.$http.post(api,vm.user).then((response) => {
            console.log(response.data);
            if(response.data.success){
                $('#modal-show').modal('show')
                $('.modal-header').addClass('bg-info').removeClass('bg-danger')
                $('.ms-content').text('Sign Success !').addClass('text-info').removeClass('text-danger').css('transform','translateY(0px) scale(1)').css('transition','.5s ease').css('opacity','1')
                $('.btn-color').css('display','none')
                $('.seconds').html(`Will transfor page after <small class="seconds-count">3</small> seconds for you`)
                $('.modal-footer').addClass('justify-content-center')
                setTimeout(()=> {
                  $('.ms-content').text('Sign Success !').css('transform','translateY(100px) scale(0.1)').css('transition','.5s ease').css('opacity','0')
                  $('.seconds-count').text('2')
                },1000)
                setTimeout(()=> {
                  $('.ms-content').text('Welcome !').css('transform','translateY(0px) scale(1)').css('transition','.5s ease').css('opacity','1')
                  $('.seconds-count').text('1')
                },2000)
                setTimeout(()=> {
                  $('.seconds').html(``)
                  $('#modal-show').modal('hide')
                  vm.$router.push('/control/products') // 3 秒後進入產品頁面
                },3000)
            }else{
              vm.user.username = null
              vm.user.password = null
              $('#modal-show').modal('show')
              $('.modal-header').addClass('bg-danger').removeClass('bg-info')
              $('.ms-content').text('Sign false !').addClass('text-danger').removeClass('text-info').css('transform','translateY(0px) scale(1)').css('transition','.5s ease').css('opacity','1')
              $('.btn-color').css('display','block').addClass('btn-danger')
              $('.modal-footer').removeClass('justify-content-center')
              setTimeout(()=>
              $('.ms-content').text('Sign false !').css('transform','translateY(100px) scale(0.1)').css('transition','.5s ease').css('opacity','0'),1000)
              setTimeout(()=>
              $('.ms-content').text('Try Again !').css('transform','translateY(0px) scale(1)').css('transition','.5s ease').css('opacity','1'),2000)
              setTimeout(()=>
              $('.btn').bind('click',function(){
                $('#modal-show').modal('hide')
              }),3000)
            }
          })
        },
  },
}
</script>

<style scoped>
    html,
body {
  height: 100%;
}

body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #f5f5f5;
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
}
.form-signin .checkbox {
  font-weight: 400;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
</style>



