<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <div class="my-5 row justify-content-center">
            <form class="col-md-8" @submit.prevent="payOrder">
                <table class="table">
                    <thead>
                        <th>Product</th>
                        <th>Count</th>
                        <th>Price</th>
                    </thead>
                    <tbody>
                        <tr v-for="list in order.products" :key="list.id">
                            <td class="align-middle">{{ list.product.title }}</td>
                            <td class="align-middle">{{ list.qty }}/{{ list.product.unit }}</td>
                            <td class="align-middle text-right">{{ list.final_total }} US</td>
                        </tr>
                    </tbody>
                    <tfoot>
                        <tr>
                            <td colspan="2" class="text-right fa-2x">Total</td>
                            <td class="text-right"><strong class="text-danger fa-2x">{{ order.total }} US</strong></td>
                        </tr>
                    </tfoot>
                </table>
                <table class="table">
                    <tbody>
                        <tr>
                            <th width="100">Email</th>
                            <td>{{ order.user.email }}</td>
                        </tr>
                        <tr>
                            <th>Name</th>
                            <td>{{ order.user.name }}</td>
                        </tr>
                        <tr>
                            <th>Telephone</th>
                            <td>{{ order.user.tel }}</td>
                        </tr>
                        <tr>
                            <th>Address</th>
                            <td>{{ order.user.address }}</td>
                        </tr>
                        <tr>
                            <th>Payment</th>
                            <td>
                                <span v-if="!order.is_paid" class="text-danger">Unpaid</span>
                                <span v-else class="text-success">Paid</span>
                            </td>
                        </tr>
                    </tbody>
                </table>
                <div class="text-right" v-if="order.is_paid === false">
                    <button class="btn btn-danger">Confirm to pay</button>
                </div>
            </form>
        </div>
        <Modal/>
    </div>
</template>

<script>
import $ from 'jquery'
import Modal from '../Modal'
export default {
    data(){
        return {
            // 存放 orderId 回傳物件
            order:{
                user:{}
            },
            // 存放 orderId 的頁面 id
            orderId:'',
            isLoading:false
        }
    },
    components:{
        Modal
    },
    methods:{
        // 取得訂單資料
        getOrder(){
            const vm = this
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order/${vm.orderId}`;
            vm.isLoading = true
            this.$http.get(api).then((response) => {    
            vm.order = response.data.order
            console.log(response)
            vm.isLoading = false
            })
        },
        // 訂單付款
        payOrder(){
            const vm = this
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/pay/${vm.orderId}`;
            this.$http.post(api).then((response) => {    
            console.log(response)
            $('#modal-show').modal('show')
            $('.ms-content').html(`Paid success<br>Thanks for your bought !`)
            $('.btn-close').css('display','block').addClass('btn-info').removeClass('btn-danger')
            $('.modal-footer').addClass('justify-content-center')
            $('.seconds').text('The modal will close in 1 seconds')
            $('.btn-close').css('display','none')
                if(response.data.success){
                    setTimeout(()=>{
                        $('#modal-show').modal('hide')
                    },1000)
                    setTimeout(()=>{
                        vm.isLoading = true
                    },2000)
                    setTimeout(()=>{
                        vm.getOrder()// 重新取得/刷新付款頁面
                        vm.isLoading = false
                    },3000)
                }
            })
        }
    },
    created(){
        this.orderId = this.$route.params.orderId
        console.log(this.orderId)
        this.getOrder() // 取得訂單資料
    }
}
</script>


