<template>
    <div>
        <h3 class="mt-5 text-center">Order information</h3>
        <div class="row justify-content-center">
            <form class="col-md-6" @submit.prevent="createOrder">
                <div class="form-group">
                    <label for="useremail">Email</label>
                    <input type="email" class="form-control" name="email" id="useremail"
                        v-model="form.user.email" v-validate="'required|email'" :class="{'is-invalid': errors.has('email') }" placeholder="please enter Email">
                    <span class="text-danger" v-if="errors.has('email')">{{ errors.first('email')}}</span>
                </div>
                <div class="form-group">
                    <label for="username">Name</label>
                    <input type="text" class="form-control" name="name" id="username"
                        v-model="form.user.name" v-validate="'required'" :class="{'is-invalid': errors.has('name') }" placeholder="please enter name">
                    <span class="text-danger" v-if="errors.has('name')">The name field must be a valid name</span>
                    <span class="text-success" v-if="form.user.name !== ''">The name field valid</span>
                </div>
                <div class="form-group">
                    <label for="usertel">Telephone</label>
                    <input type="tel" class="form-control" name="tel" v-validate="'required'" id="usertel" v-model="form.user.tel" :class="{'is-invalid': errors.has('tel') }" placeholder="please enter telephone">
                    <span class="text-danger" v-if="errors.has('tel')">The tel number field must be a valid number</span>
                    <span class="text-success" v-if="form.user.tel !== ''">The tel number field valid</span>
                </div>
                <div class="form-group">
                    <label for="useraddress">Address</label>
                    <input type="text" class="form-control" name="address" v-validate="'required'" id="useraddress" v-model="form.user.address" :class="{'is-invalid': errors.has('address') }" placeholder="please enter address">
                    <span class="text-danger" v-if="errors.has('address')">The address field must be a valid address</span>
                    <span class="text-success" v-if="form.user.address !== ''">The address field valid</span>
                </div>
                <div class="form-group">
                    <label for="comment">Message</label>
                    <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message" placeholder="please enter message or not"></textarea>
                </div>
                <div class="text-right">
                    <button class="btn btn-danger">Send Order List</button>
                </div>
            </form>
        </div>
        <Modal/>
    </div>
</template>

<script>
import $ from 'jquery'
import Modal from '../Modal'

export default {
    data(){
        return{
            // 儲存回傳表單內容
            form:{
                user:{
                    name:'',
                    email:'',
                    tel:'',
                    address:''
                },
                message:''
            },
        }
    },
    components:{
        Modal
    },
    methods: {
        createOrder(){
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
            const vm = this
            const order = vm.form
            this.$validator.validate().then((result)=>{// 送出前驗證表單
                if(result){
                    $('#modal-show').modal('show')
                    $('.modal-header').addClass('bg-info').removeClass('bg-danger')
                    $('.ms-content').text('Order list create success !')
                    $('.modal-footer').addClass('justify-content-center')
                    $('.btn-close').css('display','none')
                    $('.seconds').text('Transfor page for you')
                    this.$http.post(api,{ data:order }).then((response) => {    
                    if(response.data.success){
                        $('#modal-show').modal('hide')
                        $('.seconds').text('')
                        setTimeout(()=> vm.$router.push(`/checkout/${response.data.orderId}`),1000)
                    }
                    })
                } else {
                    $('#modal-show').modal('show')
                    $('.modal-header').addClass('bg-danger').removeClass('bg-info')
                    $('.ms-content').html(`The form field must be a valid field</br>or can't be empty`)
                    $('.seconds').text('')
                    $('.modal-footer').removeClass('justify-content-center')
                    $('.btn-close').css('display','block').addClass('btn-danger')
                    $('.btn-close').bind('click',()=>{
                        $('#modal-show').modal('hide')
                    })
                }
            })
            
        }
    },
}
</script>


