<template>
    <div>    
        <nav class="navbar navbar-dark sticky-top bg-dark flex-md-nowrap p-0 shadow">
            <a class="navbar-brand col-md-3 col-lg-2 mr-0 px-3 text-center d-block" href="#"><i class="fad fa-star-of-david text-color"></i>&nbspStrange Shop</a>
            <button class="navbar-toggler position-absolute d-md-none collapsed" type="button" data-toggle="collapse" data-target="#sidebarMenu" aria-controls="sidebarMenu" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <ul class="navbar-nav px-3">
                <li class="nav-item text-nowrap">
                <a class="nav-link" href="#"  @click.prevent="signOut">Sign out</a>
                </li>
            </ul>
        </nav>
        <Modal/>
    </div>
</template>
<style scope>
.text-color {
    color:yellow;
}
</style>
<script>
// 全域套用頁面元件與套件
import $ from 'jquery'
import Modal from './Modal'
export default {
    methods: {
    // 登出設定
        signOut(){
            const api = `${process.env.APIPATH}/logout`;
            const vm = this
            this.$http.post(api).then((response) => {
                console.log(response.data);
                if(response.data.success){
                    $('#modal-show').modal('show')
                    $('.modal-header').addClass('bg-info').removeClass('bg-danger')
                    $('.ms-content').text('Signout Success !').addClass('text-info').removeClass('text-danger')
                    $('.btn-color').addClass('btn-info').removeClass('btn-danger')
                    $('.modal-footer').removeClass('justify-content-center')
                    $('.btn').bind('click',function(){
                        $('#modal-show').modal('hide')
                            setTimeout(()=> vm.$router.push('/login'),800) // 0.8 秒後回到登入頁面
                    })
                }
            })
        }
    },
    components:{
        Modal
    }
}
</script>


