// 載入套件與頁面 ↓
import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '@/components/pages/Login'
import Dashboard from '@/components/Dashboard'
import Products from '@/components/pages/Products'
import Order from '@/components/pages/Order'
import Coupon from '@/components/pages/Coupon'
import OrderMain from '@/components/pages/OrderMain'
import Checkout from '@/components/pages/Checkout'

// 全域套用套件
Vue.use(VueRouter)
var ms = 1
    // router 頁面設定細節
export default new VueRouter({
    routes: [{
        // 當亂輸入網址時會自動導正網址為/login
        path: '*',
        redirect: '/login',
    }, {
        path: '/login',
        name: 'Login',
        component: Login
    }, {
        path: '/control',
        name: 'Dashboard',
        component: Dashboard,
        children: [{
            path: 'products',
            name: 'Products',
            component: Products,
            meta: { requiresAuth: true } // 驗證登入，如無登入無法操作
        }, {
            path: 'coupon',
            name: 'Coupon',
            component: Coupon,
            meta: { requiresAuth: true } // 驗證登入，如無登入無法操作
        }, {
            path: 'order',
            name: 'Order',
            component: Order,
            meta: { requiresAuth: true } // 驗證登入，如無登入無法操作
        }]
    }, { // 模擬購物車頁面設定
        path: '/',
        name: 'Dashboard',
        component: Dashboard,
        children: [{
                path: 'ordermain',
                name: 'OrderMain',
                component: OrderMain
            },
            {
                path: 'checkout/:orderId',
                name: 'Checkout',
                component: Checkout
            }
        ]
    }]
})


