<template>
    <div>
    <loading :active.sync="isLoading"></loading>
        <div class="text-right">
            <button class="btn btn-primary mt-4" @click="ModalToggle(true)">Creat New Products</button>
        </div>
        <div class="table-responsive">
            <table class="table table-hover mt-4">
                <thead>
                    <tr>
                        <th width="120">Type</th>
                        <th>Product</th>
                        <th width="170">Origin price ( US )</th>
                        <th width="170">Sales price ( US )</th>
                        <th width="80">Enabled/Disable</th>
                        <th width="80">Edite</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(list,key) in products">
                        <td>{{list.category}}</td>
                        <td>{{list.title}}</td>
                        <td class="text-right">{{ list.origin_price | currency}} US</td>
                        <td class="text-right">{{ list.price | currency}} US</td>
                        <td>
                            <span v-if="list.is_enabled" class="d-block text-center text-success">Enabled</span>
                            <span v-else class='d-block text-center text-danger'>Disable</span>
                        </td>
                        <td>
                            <div class="d-flex">
                                <button class="btn btn-outline-primary btn-sm mx-1" @click="ModalToggle(false,list)">Edite</button>
                                <button class="btn btn-outline-primary btn-sm mx-1" @click="DeleteModal(list)">Delete</button>
                            </div>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        <Pagination :pages="pagination" @emitPages="getProduct"></Pagination>
          <div class="modal fade" id="open" tabindex="-1" role="dialog"aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content border-0">
                    <div class="modal-header bg-change text-white">
                        <h5 class="modal-title" id="exampleModalLabel">
                            <span class="changeText"></span>
                        </h5>
                            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="row">
                            <div class="col-sm-4">
                                <div class="form-group">
                                    <label for="image">Enter Img address</label>
                                    <input type="text" class="form-control" id="image" v-model="createProduct.imageUrl" placeholder="please enter Img address">
                                </div>
                                <div class="form-group">
                                    <label for="customFile">Or upload Img</label>
                                    <input type="file" id="customFile" class="form-control" ref="files" @change="uploadPhoto">
                                    <p class="imgST text-center mt-2"></p>
                                </div>
                                <img img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80" :src="createProduct.imageUrl" class="img-fluid" alt="">
                                <p class="imgLD text-center text-info"></p>
                            </div>
                            <div class="col-sm-8">
                                <div class="form-group">
                                    <label for="title">Title</label>
                                    <input type="text" class="form-control" id="title" v-model="createProduct.title" placeholder="please enter title">
                                </div>
                                <div class="form-row">
                                        <div class="form-group col-md-6">
                                        <label for="category">Classification</label>
                                        <input type="text" class="form-control" id="category" v-model="createProduct.category" placeholder="please enter classification">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="price">Unit</label>
                                        <input type="unit" class="form-control" id="unit" v-model="createProduct.unit" placeholder="please enter unit">
                                    </div>
                                </div>
                                <div class="form-row">
                                    <div class="form-group col-md-6">
                                        <label for="origin_price">Origin price</label>
                                        <input type="number" class="form-control" id="origin_price" v-model="createProduct.origin_price" placeholder="please enter origin price">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="price">Sales price</label>
                                        <input type="number" class="form-control" id="price" v-model="createProduct.price" placeholder="please enter sales price">
                                    </div>
                                </div>
                                <hr>
                                <div class="form-group">
                                    <label for="description">Product description</label>
                                    <textarea type="text" class="form-control" id="description" v-model="createProduct.description" placeholder="please enter product description"></textarea>
                                </div>
                                    <div class="form-group">
                                    <label for="content">Product description content</label>
                                    <textarea type="text" class="form-control" id="content" v-model="createProduct.content" placeholder="please enter product description content"></textarea>
                                </div>
                                <div class="form-group">
                                    <div class="form-check">
                                        <input class="form-check-input" type="checkbox"id="is_enabled" v-model="createProduct.is_enabled" :true-value = "1" :false-value = "0">
                                        <label class="form-check-label" for="is_enabled">
                                        <p v-if="createProduct.is_enabled" class="text-success">Enabled</p>
                                        <p v-else class="text-danger">Disable</p>
                                        </label>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">Cancel</button>
                        <button type="button" class="btn btn-close" @click="uploadProduct"></button>
                    </div>
                </div>
            </div>
        </div>
        <div class="modal fade" id="delete" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content border-0">
                    <div class="modal-header bg-danger text-white">
                        <h5 class="modal-title" id="exampleModalLabel">
                            <span>Delete Product</span>
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body text-center py-4">
                        Delete the <strong class="text-danger">"{{ createProduct.title }}"</strong> product ?
                    </div>
                    <div class="modal-footer">
                        <p class="mx-4">If you delete the item can't be recovery !</p>
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">Cancel</button>
                        <button type="button" class="btn btn-danger" @click="deleteProduct">Confirm</button>
                    </div>
                </div>
            </div>
        </div>
        <Modal/>
    </div>
</template>

<script>
import $ from 'jquery' // 一定要載入，才可辨別 jQuery 套件
import Pagination from './Pagination';
import Modal from '../Modal'

export default {
    data(){
        return{
            products:[],// 儲存回傳產品資料
            createProduct:{},// 轉儲回傳產品資料顯示於 modal
            New:false,// 產品新舊預設値
            isLoading:false,// 讀取效果預設値
            pagination:{}
        };
    },
    components:{
        Pagination,
        Modal
    },
    methods:{
        // 從 api 獲取產品資料
        getProduct(page = 1){
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/products?page=${page}`;
            const vm = this
            console.log(process.env.APIPATH,process.env.CUSTOMPATH)
            vm.isLoading = true
            this.$http.get(api).then((response) => {
            console.log(response.data);
            vm.isLoading = false
            vm.products = response.data.products
            vm.pagination = response.data.pagination
            })
        },
        // 開啟新增產品彈跳視窗
        ModalToggle(New, list){
            if(New){
                this.createProduct = {};
                this.New = true;
                $('.changeText').text('Create New Product')
                $('.bg-change').addClass('bg-info').removeClass('bg-danger')
                $('.modal-footer').removeClass('justify-content-center')
                $('.btn-close').text('Confirm').addClass('btn-info').removeClass('btn-danger')
            }else{
                this.createProduct = Object.assign({}, list);// 取消物件傳參考行為，將 list 賦予到一個空物件中
                this.New = false;
                $('.changeText').text('Change Product Content')
                $('.bg-change').addClass('bg-danger').removeClass('bg-info')
                $('.modal-footer').removeClass('justify-content-center')
                $('.btn-close').text('Update').addClass('btn-danger').removeClass('btn-info')
            }
            $('#open').modal('show');
            $('.imgST').text('None image upload').removeClass('text-danger').removeClass('text-success')
        },
        // 開起刪除產品彈跳視窗
        DeleteModal(list){
            $('#delete').modal('show');
            this.createProduct = Object.assign({}, list);
        },
        // 上傳與修改產品資料
        uploadProduct(){
            let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;
            let satuation = 'post'
            const vm = this
            if(!vm.New){
                api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.createProduct.id}`;
                satuation = 'put'
                console.log(vm.createProduct.id);
                $('#open').modal('hide');
                setTimeout(()=>{
                    $('#modal-show').modal('show')
                    $('.ms-content').text('Edite success !')
                    $('.seconds').text('The modal will close in 1 seconds')
                    $('.modal-footer').addClass('justify-content-center')
                    $('.btn-color').css('display','none')
                },800)
                setTimeout(()=>{
                    $('#modal-show').modal('hide')
                    $('.seconds').text('')
                },1700)
                setTimeout(()=>{
                    this.$http[satuation](api, {data: vm.createProduct}).then((response) => { 
                        vm.getProduct()
                    })
                },1701)
                return
            }
            console.log(process.env.APIPATH,process.env.CUSTOMPATH)
            this.$http[satuation](api, {data: vm.createProduct}).then((response) => {
            console.log(response.data);
            if(response.data.success){
                $('#open').modal('hide');
                setTimeout(()=>{
                    $('#modal-show').modal('show')
                    $('.modal-header').addClass('bg-info')
                    $('.ms-content').text('Create success !')
                    $('.seconds').text('The modal will close in 1 seconds')
                    $('.modal-footer').addClass('justify-content-center')
                    $('.btn-color').css('display','none')
                },800)
                setTimeout(()=>{
                    $('#modal-show').modal('hide')
                    $('.seconds').text('')
                },1700)
                 setTimeout(()=>{
                        vm.getProduct() // 當新增成功時重新刷新產品列表頁
                },1701)
            } else {
                $('#open').modal('hide');
                setTimeout(()=>{
                    $('#modal-show').modal('show')
                    $('.modal-header').addClass('bg-info')
                    $('.ms-content').text('Create false !')
                    $('.seconds').text('The modal will close in 1 seconds')
                    $('.modal-footer').addClass('justify-content-center')
                    $('.btn-color').css('display','none')
                },800)
                setTimeout(()=>{
                    $('#modal-show').modal('hide')
                    $('.seconds').text('')
                },1700)
                setTimeout(()=>{
                        vm.getProduct() // 當新增失敗時重新刷新產品列表頁
                },1701)
            }
            })
        },
        // 刪除產品
        deleteProduct(){
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.createProduct.id}`;
            console.log(url)
            this.$http.delete(url).then((response) => {
            console.log(response, vm.createProduct);
            $('#delete').modal('hide');
            vm.isLoading = false;
            this.getProduct();
        })
        },
        // 上傳圖檔
        uploadPhoto(){
            const uploadPhoto = this.$refs.files.files[0];
            const vm = this;
            const formSend = new FormData();
            formSend.append('file-to-upload',uploadPhoto)
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload/`
            $('.imgST').html(`Uploading&nbsp<i class="fas fa-spinner fa-spin"></i>`).addClass('text-info').removeClass('text-danger').removeClass('text-success')
            $('.imgLD').html(`<i class="fas fa-circle-notch fa-spin fa-3x"></i>`)
            vm.$set(vm.createProduct,'imageUrl');
            this.$http.post(api,formSend,{
                headers:{
                    'Content-Type':'multipart/form-data'
                    }
            }).then((response) => {
                console.log(response.data);
                if(response.data.success){
                    vm.$set(vm.createProduct,'imageUrl',response.data.imageUrl);
                    $('.imgLD').text('')
                    $('.imgST').text('Image upload success！').addClass('text-success').removeClass('text-danger').removeClass('text-info')
                }else{
                    vm.$set(vm.createProduct,'imageUrl');
                    $('.imgLD').text('')
                    $('.imgST').text('Image size is too large making to upload false！').addClass('text-danger').removeClass('text-success').removeClass('text-info')
                }
            })
        }
    },
    created() {
        this.getProduct();// 載入時取得產品資料
    },
}
</script>


