// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
// 外部套件 ↓
import Vue from 'vue'
import axios from 'axios' // 載入 axios 套件
import VueAxios from 'vue-axios' // 載入 vue-axios 套件
import Loading from 'vue-loading-overlay'; // 載入 vue-loading-overlay 套件
import VeeValidate from 'vee-validate'; // 載入 vee-validate 套件
import 'vue-loading-overlay/dist/vue-loading.css';
import 'bootstrap'

// 內部自定義套件 ↓
import App from './App'
import router from './router'
import currency from './filters/currency'
import data from './filters/data'

// 全域套用套件 ↓
axios.defaults.withCredentials = true;
Vue.config.productionTip = false
Vue.use(VueAxios, axios)
Vue.use(VeeValidate);
Vue.filter('currency', currency)
Vue.filter('data', data)
Vue.component('loading', Loading)

/* eslint-disable no-new */
new Vue({
    el: '#app',
    components: { App },
    template: '<App/>',
    router
})

// 直接更改網址會自動跳轉回登入畫面設定
router.beforeEach((to, from, next) => {
    if (to.meta.requiresAuth) {
        const api = `${process.env.APIPATH}/api/user/check`;
        axios.post(api).then((response) => {
            console.log(response.data);
            // 如果回傳結果為 success 就進入到管理頁面反之則返回登入畫面
            (response.data.success) ? next(): next({
                path: '/login',
                alert: function() {
                    alert('please resign in again !')
                }()
            })
        })
    } else {
        next() // 此行為登出時載入登入畫面
    }
})


