<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <div class="row mt-4">
            <div class="col-md-4 mb-4" v-for="list in products" :key="list.id">
                <div class="card border-0 shadow-sm">
                    <div style="height: 150px; background-size: cover; background-position: center" :style="{backgroundImage:`url(${list.imageUrl})`}"></div>
                <div class="card-body">
                    <span class="badge badge-secondary float-right ml-2">{{list.category}}</span>
                    <h5 class="card-title">
                        <a href="#" class="text-dark">{{list.title}}</a>
                    </h5>
                    <p class="card-text">{{list.content}}</p>
                    <div class="d-flex justify-content-between align-items-baseline">
                        <!-- <div class="h5">2,800 元</div> -->
                        <div class="h5" v-if="!list.price">$ {{list.origin_price}} US</div><!-- 前者顯是僅有原價 -->
                        <del class="h6" v-if="list.price">$ {{list.origin_price}} US</del><!-- 後者顯是僅有原價與優惠價 -->
                        <div class="h5" v-if="list.price">$ {{list.price}} US</div><!-- 後者顯是僅有原價與優惠價 -->
                    </div>
                </div>
                    <div class="card-footer d-flex">
                        <button type="button" class="btn btn-outline-secondary btn-sm" @click="productDetails(list.id)">
                            <i class="fas fa-spinner fa-spin" v-if="status.loading === list.id"></i>
                                Look more
                        </button>
                        <button type="button" class="btn btn-outline-danger btn-sm ml-auto" @click="addtoCart(list.id)">
                            <i class="fas fa-spinner fa-spin" v-if="status.loading === list.id"></i>
                                Add to cart
                        </button>
                    </div>
                </div>
            </div>
        </div>
        <div class="my-5 row justify-content-center">
            <div class="my-5 col-md-8">
                <table class="table">
                    <thead>
                        <th></th>
                        <th width="300">Product</th>
                        <th width="200">Count</th>
                        <th width="130">Price</th>
                    </thead>
                    <tbody>
                        <tr v-for="list in cart.carts" :key="list.id" v-if="cart.carts">
                            <td class="align-middle">
                                <button type="button" class="btn btn-outline-danger btn-sm" @click="removeCartItem(list.id)">
                                <i class="far fa-trash-alt"></i>
                                </button>
                            </td>
                            <td class="align-middle">
                                {{ list.product.title }}
                                <div class="text-success" v-if="list.coupon">
                                    Applied coupon
                                </div>
                            </td>
                            <td class="align-middle">{{ list.qty }}{{ list.product.unit }}</td>
                            <td class="align-middle text-right">{{ list.final_total }} US</td>
                        </tr>
                    </tbody>
                    <tfoot>
                        <tr>
                            <td colspan="3" class="text-right">Total</td>
                            <td class="text-right">{{ cart.total }} US</td>
                        </tr>
                        <tr v-if="cart.final_total !== cart.total">
                            <td colspan="3" class="text-right text-success">Discount price</td>
                            <td class="text-right text-success">{{ cart.final_total }} US</td>
                        </tr>
                    </tfoot>
                </table>
                <div class="input-group input-group-sm">
                    <input type="text" class="form-control" v-model="coupon" placeholder="please enter coupon code">
                    <div class="input-group-append">
                        <button class="btn btn-outline-secondary" type="button" @click="couponCode">
                            Apply coupon
                        </button>
                    </div>
                </div> 
                <p class="test"></p>       
            </div>
        </div>
        <OrderList/>
        <div class="modal fade" id="productDetails" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalLabel">{{ productDetail.title }}</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <img :src="productDetail.imageUrl" class="img-fluid" alt="">
                        <blockquote class="blockquote mt-3">
                        <p class="mb-0">{{ productDetail.content }}</p>
                        <footer class="blockquote-footer text-right">{{ productDetail.description }}</footer>
                        </blockquote>
                        <div class="d-flex justify-content-between align-items-baseline">
                        <div class="h4" v-if="!productDetail.price">{{ productDetail.origin_price }} US</div>
                        <del class="h6" v-if="productDetail.price">Origin price $ {{ productDetail.origin_price }} US</del>
                        <div class="h4" v-if="productDetail.price">Now $ {{ productDetail.price }} US</div>
                        </div>
                        <select name="" class="form-control mt-3" v-model="productDetail.num">
                            <option :value="num" v-for="num in 10" :key="num">
                                Choose {{num}} {{productDetail.unit}}
                            </option>
                        </select>
                    </div>
                    <div class="modal-footer">
                        <div class="text-muted text-nowrap mr-3">
                            Subtotal $ <strong>{{ productDetail.num * productDetail.price }}</strong> US
                        </div>
                        <button type="button" class="btn btn-primary" @click="addtoCart(productDetail.id, productDetail.num)">
                            <!--<i class="fas fa-spinner fa-spin" v-if="productDetail.id === status.loadingItem"></i>-->
                            Add to Cart
                        </button>
                    </div>
                </div>
            </div>
        </div>
        <Modal/>
    </div>
</template>

<script>
import $ from 'jquery'
import Modal from '../Modal'
import OrderList from './OrderList'

export default {
    data(){
        return{
            products:[],// 儲存回傳產品資料
            productDetail:{}, // 轉儲回傳產品資料顯示於 modal
            cart:{},
            coupon:'',
            status:{
                loading:''
            },
            isLoading:false
        }
    },
    components:{
        Modal,
        OrderList
    },
    methods: {
        // 取得產品列表
        getProduct(){
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products`;
            const vm = this
            console.log(process.env.APIPATH,process.env.CUSTOMPATH)
            vm.isLoading = true
            this.$http.get(api).then((response) => {
                console.log(response.data);
                vm.isLoading = false
                vm.products = response.data.products
            })
        },
        // 轉存產品列表顯示於 modal
        productDetails(id){
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
            const vm = this
            console.log(process.env.APIPATH,process.env.CUSTOMPATH)
            vm.status.loading = id
            this.$http.get(api).then((response) => {
                vm.productDetail = response.data.product
                $('#productDetails').modal('show')
                console.log(response);
                vm.status.loading = ''
        })
        },
        // 新增購物車內容
        // qty 為 ES6 設定預設値
        addtoCart(id, qty = 1){
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
            const vm = this
            console.log(process.env.APIPATH,process.env.CUSTOMPATH)
            vm.status.loading = id
            const cart = {
                product_id:id,
                qty
            }
            this.$http.post(api,{ data:cart }).then((response) => {
                console.log(response);
                vm.status.loading = ''
                vm.getCart();
                $('#productDetails').modal('hide');
        })
        },
        // 取得購物車內容
        getCart(){
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
            const vm = this
            console.log(process.env.APIPATH,process.env.CUSTOMPATH)
            vm.isLoading = true
            this.$http.get(api).then((response) => {
                console.log(response);
                vm.cart = response.data.data
                console.log(vm.cart.carts)
                vm.isLoading = false
            })
        },
        // 刪除購物車內容
        removeCartItem(id){
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
            const apiCart = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
            const vm = this
            console.log(process.env.APIPATH,process.env.CUSTOMPATH)
            vm.isLoading = true
            this.$http.get(apiCart).then((response) => {
                if(response.data.success){
                    $('#modal-show').modal('show')
                    $('.ms-content').text('Do you want to delete the product ?')
                    vm.isLoading = false
                }
            })
            $('.btn-close').bind('click',()=>{
                this.$http.delete(api).then((response) => {
                    console.log(response);
                    $('#modal-show').modal('hide')
                    setTimeout(()=>{
                        $('#modal-show').modal('show')
                        $('.ms-content').text('This product has been remove from your cart !')
                    },1000)
                    setTimeout(()=>{
                        $('#modal-show').modal('hide')
                        vm.getCart();
                    },2500)
                })
            })
        },
        // 輸入優惠券折抵
        couponCode(){
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
            const vm = this
            const coupon = {
                code:vm.coupon
            }
            console.log(process.env.APIPATH,process.env.CUSTOMPATH)
            vm.isLoading = true
            this.$http.post(api,{ data:coupon }).then((response) => {    
                console.log(response)
                if(response.data.success){
                    $('.test').text('Valid coupon code').addClass('text-success').removeClass('text-danger')
                    vm.getCart()
                    vm.isLoading = false
                } else {
                    $('.test').text('Invalid coupon code').addClass('text-danger').removeClass('text-success')
                    vm.getCart()
                    vm.isLoading = false
                }
            })
        },
        // 建立訂單
        createOrder(){
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
            const vm = this
            const order = vm.form
            vm.isLoading = true
            this.$http.post(api,{ data:order }).then(() => {    
            vm.isLoading = false
            })
        }
    },
    created() {
        this.getProduct()
        this.getCart()
    },
}
</script>


